<template>
  <div>
    <table>
      <tr>
        <th>{{languages.a18[langIndex]}}</th>
        <th>{{languages.a19[langIndex]}}</th>
      </tr>
      <tr v-for="count in line" :key="count">
        <td>{{count}}</td>
        <td class="table_click" :class="floorbool[count]==true?'tablecolor':''" v-if="!type" @click="currentFloor(count)">{{value[count]}}</td>
        <td v-else-if="type=='input'">
          <van-field
            class="table_input"
            input-align="center"
            v-model="value[count]"
            @input="changeTable()"
          />
        </td>
      </tr>
    </table>
  </div>
</template>

<style>
.table_input .van-field__control {
  width: 3rem;
  font-size: 0.4rem;
}
</style>

<style scoped>
table {
  border-collapse: collapse;
  border: 0.04rem solid black;
}
th,
td {
  border: 1px solid black;
  padding: 0.2rem 0.4rem;
}

td {
  text-align: center;
}
table {
  border-collapse: collapse;
}
.van-cell {
  padding: 0;
}
.tablecolor{
  color: green;
}
.table_click{
  width: 3rem;
  font-size: 0.4rem;
}
</style>

<script>
import { Encrypt } from "@/model/utils.js";

export default {
  name: "Tables",
  props: ["line", "type"],
  data() {
    return {
      value: {}
    };
  },
  methods: {
    changeTable() {
      this.$emit("tableValue", this.value); //向父组件传值
    },
    currentFloor(e) {
      if(this.floorbool[e]==true){
        return;
      }
      let obj = {
        floorMap: this.floormap,
        floorNum: this.tablenum,
        currentFloor: e + ""
      };
      let value = Encrypt(JSON.stringify(obj));
      this.$router.push({
        name: "Lopcode",
        params: { encryption: value, floorname: e }
      });
    }
  },
  computed: {
    languages() {
      return this.$store.state.languages;
    },
    langIndex() {
      return this.$store.state.langIndex;
    },
    tabledata() {
      return this.$store.state.tabledata;
    },
    floormap() {
      return this.$store.state.floormap;
    },
    tablenum() {
      return this.$store.state.tablenum;
    },
    floorbool(){
      return this.$store.state.floorbool;
    }
  },
  mounted() {
    if (this.tabledata == null) {
      let boolobj = {};
      for (let i = 1; i <= this.line; i++) {
        this.value[i] = null;
        boolobj[i] = false;
      }
      this.$store.commit('getFloorbool', boolobj);
    } else {
      this.value = this.tabledata;
    }
  }
};
</script>